@import "../../variable.scss";
.outlet-container {
    min-width: 600px;
    max-width: 600px;
    display: flex;
    border: 1px solid #dbdbdb;
    max-height: 600px;
    overflow: hidden;
    box-shadow: 0px 0px 25px 10px rgba(0, 0, 0, 0.1);
}

.city-selection-container {
    width: 50%;
    background-color: #f7f7f7;
    overflow: auto;
}

.outlet-selection-container {
    width: 50%;
    background-color: white;
    overflow: auto;
}

.ui.input {
    display: flex !important;
}

.outlet-filter-list-item {
    padding: $spacing-m;
    outline: none;
}

.outlet-sticky-section {
    position: sticky;
    top: 0;
    background-color: inherit;
    z-index: 2;
}

.non-selected-item {
    border-left: 6px solid transparent;
}

.selected-item {
    position: relative;
    border-left: 6px solid var(--primary-blue);
    color: var(--primary-blue);
}


